  safetyStockCheck(data) {
    return new Promise(function (reslove, reject) {
      axios({
        url: "v2/risk/VerifyBills/getVerifyBills",
        method: "post",
        data: data
      })
        .then(({ data }) => {
          if (data.code === 200) {
            reslove(data);
          } else if (data.code === 362) {
            MessageBox.alert(
              "" + data.data + "",
              "",
              {
                confirmButtonText: "",
                callback: action => { }
              }
            );
          } else if (data.code === 363) {
            MessageBox.confirm(
              "" +
              data.data +
              "",
              "",
              {
                distinguishCancelAndClose: true,
                confirmButtonText: "",
                cancelButtonText: ""
              }
            )
              .then(() => {
                reslove(data);
              })
              .catch(action => { });
          }
        })
        .catch(msg => {
          reslove(data);
        });
    });
  },
